/**
* border radius
*/

.btn , .jumbotron ,.nav :hover {
    border-radius:0px;
}

/* cart badge*/

.cart-badge, .cart-badge:hover{
    border-radius: 50%;
    padding: 4px;
    font-size: 12px;
    font-style: italic;
    background: #000;
}

/**
* product image on card
*/

.product-img{
    min-height: 100px;
}

.jumbotron h2{
    margin-top: -20px;
}

@media only screen and (max-width:600px) {
    .jumbotron h2{
        margin-top: 10px;
    }
}

/**
* single product page - product name
*/

.name {
    background:indigo;
    color: #fff;
    font-weight: bold;
}

/**
* black shade form 10-1
*/
.black-10 {
    background: #f2f2f2;
}

.black-9 {
    background: #e6e6e6;
}

.black-8 {
    background: #d9d9d9;
}

.black-7 {
    background: #cccccc;
}

.black-6 {
    background: #bfbfbf;
}

.black-5 {
    background: #b3b3b3;
}

/**
* jumbotron animation
*/

.jumbotron {
    width: 30wh;
    height: 30vh;
    color: #fff;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    -webkit-animation: Gradient 15s ease infinite;
    -moz-animation: Gradient 15s ease infinite;
    animation: Gradient 15s ease infinite;
}

/* Safari å’Œ Chrome */
@-webkit-keyframes Gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

 /* Firefox */
@-moz-keyframes Gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

/* Opera */
@keyframes Gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}